import { PartialTheme } from "@fluentui/react"

export let prefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;

export const currentTheme = () =>{
  if(prefersDark){
  return darkTheme}
  else {return lightTheme}
}

